body {
    background-color: #f8f8f8;
  }
  
  .condo-dash-container {
    margin-left: 100px;
    margin-right: 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: "Roboto-Regular", Helvetica;
  }
  
  .condo-dash-top {
    margin-top: 10px;
    color: black;
  }
  
  .condo-dash-icon {
    width: 12px;
    margin-right: 8px;
    margin-bottom: 3px;
  }
  
  .condo-dash-location-name {
    display: inline-block;
    font-size: 14px;
  }
  
  .condo-dash-second-row,
  .condo-dash-third-row,
  .condo-dash-fourth-row,
  .condo-dash-fifth-row,
  .condo-dash-sixth-row {
    display: flex;
  }
  
  .condo-dash-profile-box {
    border: 1px solid #0000001a;
    background-color: white;
  
    width: 40%;
    margin-left: 6%;
    margin-top: 12%;
    margin-bottom: 10.7%;
    max-width: 280px;
    max-height: 420px;
  
    border-radius: 5px;
  
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .condo-dash-face {
    margin-top: 5%;
    width: 40%;
    padding-bottom: 40%;
    /* max-width: 200px; */
    /* height: 0; */
    border-radius: 50%;
    overflow: hidden;
    position: relative;
  }
  
  .condo-dash-face img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
  }
  .condo-dash-edit-button {
    margin-top: 1%;
    color: #0075ff;
    text-decoration: none;
    font-size: 1vw;
  }
  
  .condo-dash-name {
    font-weight: 500;
    font-size: 1.1vw;
    margin-top: 3.5%;
    color: black;
    margin-bottom: 0;
  }
  
  .condo-dash-type {
    color: #00000080;
    font-weight: 500;
    font-size: 1.1vw;
  }
  
  .condo-dash-phone {
    border-radius: 5px;
    width: 72%;
    height: 9.5%;
    margin-bottom: 6%;
    background-color: rgb(22, 212, 22);
  
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  
  .condo-dash-phone p {
    display: inline-block;
    font-size: 0.95vw;
    margin-left: 6%;
    margin-right: 15%;
    margin-bottom: 0;
    color: white;
  }
  .condo-dash-phone img {
    width: 9%;
  }
  
  .condo-dash-email {
    border-radius: 5px;
    width: 72%;
    height: 9.5%;
    border: 1px solid #434faa;
  
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  
  .condo-dash-email p {
    color: #434faa;
    display: inline-block;
    font-size: 0.95vw;
    margin-left: 10%;
    margin-right: 12%;
    margin-bottom: 0;
  }
  
  .condo-dash-email img {
    width: 9%;
  }
  
  .condo-dash-info-box {
    margin-top: 2%;
    background-color: white;
    border: 1px solid #0000001a;
    width: 70%;
    max-width: 900px;
    padding: 2.5%;
    border-radius: 5px;
  }
  
  .condo-dash-headertwo {
    color: black;
    font-weight: 600;
    font-size: 1.3vw;
    margin-bottom: 2%;
  }
  
  .condo-dash-info-left1 {
    display: inline-block;
    margin-right: 5%;
  }
  
  .condo-dash-info-left2 {
    display: inline-block;
    position: relative;
    bottom: 53.5%;
    margin-right: 5%;
  }
  
  .condo-dash-info-left1 p,
  .condo-dash-info-left2 p,
  .condo-dash-explanation,
  .condo-dash-feature p {
    color: #00000080;
    font-size: 1vw;
  }
  
  .condo-dash-info-right1 {
    display: inline-block;
    margin-right: 25%;
  }
  .condo-dash-info-right2 {
    display: inline-block;
    position: relative;
    bottom: 53.5%;
  }
  .condo-dash-info-right1 p,
  .condo-dash-info-right2 p {
    color: black;
    font-size: 1vw;
  }
  
  .condo-dash-financial-and-request-box {
    border: 1px solid #0000001a;
    background-color: white;
    border-radius: 5px;
    width: 30%;
    max-width: 280px;
    margin-left: 6%;
    position: relative;
  
    margin-top: 8%;
    margin-bottom: 2.5%;
  }
  
  .condo-dash-financial-status,
  .condo-dash-maintenance {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  
    margin-top: 5px;
  }
  
  .condo-dash-financial-status p {
    display: inline-block;
    font-size: 1.2vw;
    background-color: black;
    color: white;
    border-radius: 40px;
    padding: 1% 5.4%;
    margin-top: 10px;
  
    margin-right: 15%;
    margin-left: 5%;
  }
  
  .condo-dash-financial-status img {
    width: 12%;
    margin-left: 10%;
  }
  
  .condo-dash-condo-fees,
  .condo-dash-requests {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  
    border-bottom: 1px solid #0000001a;
  }
  
  .condo-dash-condo-fees p {
    display: inline-block;
    font-size: 1vw;
    margin-top: 15px;
  }
  
  .condo-dash-condo-fees img {
    display: inline-block;
    width: 7%;
    margin-left: 3%;
    margin-right: 3%;
  }
  
  .condo-dash-payment-history {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
    margin-top: 10px;
    font-size: 1vw;
  }
  
  .condo-dash-view-link {
    background-color: red;
    color: white;
    text-decoration: none;
    border-radius: 40px;
    font-weight: 600;
    padding: 1% 15%;
    margin-top: -15px;
  }
  
  .condo-dash-feature {
    display: inline-block;
    width: 50%;
  }
  
  .condo-dash-feature p {
    margin-bottom: 0;
  }
  
  .condo-dash-maintenance p {
    display: inline-block;
    font-size: 1.1vw;
    background-color: black;
    color: white;
    border-radius: 40px;
    padding: 1% 5.5%;
    margin-top: 10px;
  
    margin-right: 7%;
    margin-left: 2%;
  }
  
  .condo-dash-maintenance img {
    width: 12%;
    margin-left: 5%;
  }
  
  .condo-dash-nb-request {
    font-size: 0.85vw;
    color: #3dacf0;
    width: 50%;
    margin: 2.5%;
  }
  
  .condo-dash-progress {
    font-size: 0.55vw;
    border-radius: 40px;
    margin: 2.5%;
    color: black;
  }
  
  .condo-dash-submit {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
    margin-top: 7%;
  }
  
  .condo-dash-request-link {
    background-color: red;
    color: white;
    text-decoration: none;
    border-radius: 40px;
    font-weight: 600;
    padding: 1.5% 15%;
    font-size: 0.91vw;
  }
  
  @media (max-width: 771px) {
    .condo-dash-info-right1 {
      margin-right: 10%;
    }
  }
  
  @media (max-width: 600px) {
    .condo-dash-phone p,
    .condo-dash-email p {
      font-size: 0.78vw;
    }
  
    .condo-dash-financial-status p,
    .condo-dash-maintenance p {
      font-size: 1.04vw;
    }
  }
  